class ViewBookComponent {
    constructor(http, bookService) {
        this.http = http;
        this.bookService = bookService;
        this.bookObj = {};
        this.category = [];
        this.books = [];
    }
    ngOnInit() {
        //  this.getBooks();
        this.get_category();
    }
    getBooks() {
        this.bookService.getMyData().subscribe(res => {
            this.bookObj = res;
        });
    }
    deleteData(id) {
        //console.log(id);
        var result = confirm("Are you sure you want to delete this item..?");
        if (result) {
            this.bookService.deleteData(id).subscribe(res => {
                this.getBooks();
            });
        }
    }
    get_category() {
        this.bookService.get_category().subscribe(res => {
            this.category = res;
        });
    }
    selectcat(event) {
        const newVal = event.target.value;
        this.http.get('http://127.0.0.1:8000/api/selectCategory/' + newVal).subscribe(res => {
            if (Array.isArray(res)) {
                // this.bookObj = {}
                this.books = res;
            }
            else {
                this.books = [];
                const data = res[Object.keys(res)[0]];
                // this.bookObj = data;
                this.books.push(data);
            }
        });
    }
}
