class CartItemsComponent {
    // id: any;
    // userId:any;
    // totalamount = 0;
    constructor(http, ServCart) {
        this.http = http;
        this.ServCart = ServCart;
        this.bookObj = {};
    }
    ngOnInit() {
        this.ServCart.getCartData();
        console.log(this.ServCart);
    }
    deleteCartData(id) {
        //console.log(id);
        var result = confirm("Are you sure you want to delete this item..?");
        if (result) {
            this.ServCart.deleteCartData(id).subscribe(res => {
                this.ServCart.getCartData();
            });
        }
    }
}


