class RegisterpageComponent {
    constructor(http) {
        this.http = http;
        this.districts = [];
        this.userObj = {};
    }
    ngOnInit() {
    }
    get_districts() {
        this.http.get('http://127.0.0.1:8000/api/getDistricts/').subscribe(res => {
            this.districts = res;
        });
    }
    fn_signup() {
        console.log(this.userObj);
        this.http.post('http://127.0.0.1:8000/api/insert1/', this.userObj).subscribe(res => {
            if (res == 1) {
                this.userObj = {};
            }
        });
    }
}


